.player-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: var(--notebook-bg-color-player);
  position: relative;
  border-radius: 8px;
  border-style: solid;
  border-color: rgb(133, 143, 185);
  border-width: 4px;
  transition: .1s;
  ;

  &:hover {
    transform: scale(1.05);
    background-color: var(--notebook-bg-color-player-highlight);
  }

  .more-info {
    position: absolute;
    top: 8px; // Adjust as needed
    right: 10px; // Adjust as needed
    border-style: solid;
    border-color: var(--info-display-pen);
    border-width: 2px;
  }

  &.selected {
    transform: scale(1.05);
    background-color: var(--notebook-bg-color-player-selected);
    border-color: rgb(68, 108, 164);
  }

  .player-schedule-container {
    margin-top: 4px;
    margin-right: 0px;
    left: 0px;
    width: 80%;
    left: 10%;

    .day-square {
      color: var(--notebook-text-color);
      border-width: 3px;
      max-height: 24px;
      min-width: 5%;
      border-style: solid;
      position: relative;
      display: flex;
      justify-content: center;
      font-weight: bold;

      .day-percentage {
        display: none;
      }

      .day-symbol {
        font-size: 0.65em;
      }

      .day-abbreviation {
        font-size: 0.6em;
        position: absolute;
        bottom: -25px;
        font-style: normal;
      }
    }
  }

  .player-card-header {
    display: flex;
    align-items: center;
    width: 100%;

    .player-icon {
      flex: 0 0 20%; // Take up 20% of the horizontal space
      max-width: 20%; // Ensure it does not exceed 20%
	    max-height: 100%;;
			position: relative;
     
			.cimage {
				width: 100%;
        border-radius: 100%;
			}
    }

    .player-upper-info {
      flex: 1; // Take up the remaining space
      max-width: 80%; // Ensure it does not exceed 80%
      display: flex;
      flex-direction: column;

      .player-name {
        font-size: var(--notebook-header-font-size-xsmall);
        font-style: normal;
        font-weight: bold;
        color: var(--notebook-text-color);
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }
  }

  .player-preferences-section {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px 0;
    margin-top: 10px;
    color: var(--notebook-text-color);

    .preferences-column {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;

      .preferences-title {
        font-weight: bold;
        text-decoration: underline;
      }

      .preferences-main {
        margin: 5px 0;
		    display: none;
      }

      .preferences-dots {
        display: flex;
        gap: 5px;
        margin-top: 8px;

        .preference-dot {
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background-color: var(--notebook-color-conflicted-main);

          &.like {
            background-color: var(--notebook-color-like-main);
          }

          &.hate {
            background-color: var(--notebook-color-hate-main);
          }
        }
      }

      .preferences-traits {
        display: flex;
        flex-direction: column;
        align-items: center;

        .trait-item {
          margin: 5px 0;
          font-weight: bold;
        }
      }

      .tooltip {
        position: absolute;
        bottom: 30px;
        transform: translateX(-50%);
        background-color: white;
        border: 3px solid #ccc;
        border-radius: 8px;
        padding: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        z-index: 10;
        border-style: solid;
        pointer-events: none;
        &.like {
          position: absolute;
          border-color: green;
          background-color: #c3e0c4;
          left: 50%;
        }
        &.hate {
          position: absolute;
          border-color: rgb(128, 28, 0);
          background-color: #edbab1;
          right: -50%;
        }

        &.traits {
          position: absolute;
          border-color: rgb(135, 37, 155);
          background-color: #e2b4e4;
          left: 150%;
          width: 300%;
        }

        .tooltip-content {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 5px;

          .tooltip-pill {
            background-color: #f0f0f0;
            border-radius: 15px;
            padding: 5px 10px;
            text-align: center;
          }
        }
        .tooltip-content-single {
          display: grid;
          grid-template-columns: 1fr;
        }
      }
    }
  }

}

.player-schedule-container {
  margin-top: 70px;
  margin-right: 20px;
  left: -40px;
  font-size: xx-large;
  width: 100%;
  text-align: center;
  position: relative;
  max-width: 100%; // Ensure the width does not exceed the parent bounds

  .player-schedule-title {
    position: relative;
    font-size: var(--notebook-header-font-size-small);
    font-style: normal;
    font-weight: bold;
    margin-bottom: -15px;
  }

  .schedule {
    width: 100%; // Ensure the schedule takes up the full width of the container
    max-width: 100%; // Ensure the schedule does not exceed the container bounds
    overflow: hidden; // Prevent content from overflowing
  }

  .week-display-days {
    overflow: visible;
  }

  .week-days {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .day-square {
    color: var(--notebook-text-color);
    // font-weight: bold;
    // border-radius: 5px;
    // border-color: var(--notebook-text-color);
    border-width: 5px;
    border-style: solid;
    margin: 5px;
    position: relative;
    display: flex;
    justify-content: center;
    font-weight: bold;

    .day-percentage {
      display: none;
    }

    .day-abbreviation {
      font-size: 1.2em;
      position: absolute;
      bottom: -49px;
      font-style: normal;
    }
  }
}
