@keyframes pulsate {
    0% {
      transform: scale(1);
      box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
      background-color: rgba(229, 0, 0, 0.5);
      border-width: 1px;
    }
    50% {
      transform: scale(3.3);
      box-shadow: 0 0 30px rgb(212, 0, 0);
      border-width: 100px;
      background-color: rgba(229, 0, 0, 0.5);
    }
    100% {
      transform: scale(1);
      box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
      border-width: 1px;
      background-color: rgba(15, 171, 18, 0.5);
    }
  }


.cimage {
	height: 100%;
}

.menu-button {
   
	margin-top: 20px;
    height: 100px;
    width: 100%;
    padding: 14px;
	font-size: 1.5em;
    background-color: #81aee3;
    color: #fff;
    border: none;
    border-radius: 5px;
	transition: .5s;
    cursor: pointer;
	.label-text {
		font-size: 1.2em;
		top: 25%;
		position: relative;
	}
}

.menu-button:hover {
	background-color: #3f51b5;
	transform: scale(1.05);
}

.more-info {
    position: absolute;
	transition: .5s;
    right: 10px;
    top: 10px;
	min-width: 20px;
	min-height: 20px;
	color: white;
	border-radius: 100%;
    background: var(--info-display-pen);
	opacity: .5;
	cursor: pointer;
    &:hover {
        transform: scale(1.3);
        opacity: 1;
    }
}

.nav-bar-container {
	display: flex;
	flex-direction: row;
	justify-content:space-evenly;
    
}


.nav-button {
  
	margin-top: 20px;
	
	padding: 14px;
    height: 140px; /* Ensure the height matches the footer height */
    width: 140px; /* Ensure the width matches the height for round buttons */
    border-radius: 100%;  
    border-style: solid;
    color: white;
	border-color: grey;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    background-color: #ffffff00;
    cursor: pointer;
    transition: .5s;
    &.highlight {
        animation: pulsate .8s infinite; // Apply the pulsate animation
        
        border-width: 5px;
        border-color: white;
      }
    &:hover {
      transform: scale(1.1);
      border-width: 10px;
      border-color: white;
    }

  }
 
 