html, body {
	height: 100%;
	margin: 0;
	padding: 0;
  }
  
  .mode-container {
	position: relative;
	height: 100vh; /* Ensure the container takes the full viewport height */
	width: 100%;
	overflow: hidden;
	background-color: #13161e;
  }
  
  .mode-background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: transform 1s ease; /* Smooth transition */
  
	.mode-background-image {
	  position: absolute;
	  top: 0;
	  left: 50%;
	  transform: translateX(-50%);
	  height: 100%;
	  background-size: contain;
	  background-position: center;
	  background-repeat: no-repeat;
	}
  
	.mode-background-bottom {
		position: absolute;
		top: 75%;
		width: 100%;
		height: 100%;
		left: 0%;
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		transform: translateX(0%);
	 
	}
  }
  
  .mode-background.slide-left {
	transform: translateX(-50%);
  }

  .mode-background.left {
	transform: translateX(-50%);
  }

  .mode-background.slide-right {
	transform: translateX(50%);
  }

  .mode-background.right {
	transform: translateX(50%);
  }

  .mode-background.slide-down {
	transform: translateY(-75%);
  }

  .mode-background.down {
	transform: translateY(-75%);
  }

  .mode-component {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	color: #fff;
	text-align: center;
  }

	.state-main-menu {
		height: 50%;
		width: 50%;
		background-color: black;
	}

.campaign-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 20px; /* Adjust as needed to position it to the right of the player list */
  margin-right: 20px;
}

.campaign-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 600px; /* Adjust as needed */
  padding: 10px 20px;
  margin: 10px 0;
  border-radius: 25px;
  background-color: #2c2f36;
  color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.campaign-item:hover {
  background-color: #3a3d46;
}

.campaign-item .campaign-name {
  font-size: 1.2em;
  font-weight: bold;
}

.campaign-item .campaign-details {
  font-size: 0.9em;
  color: #b0b3b8;
}
