.character-creation-container {
	display: flex;
	height: 100%;
	overflow: hidden;
	
	.character-creation-left-container
	{
		display: flex;
		flex-direction: column;
		width: 40%;

		.nav-bar-container {
			position: relative;
			left: 20%;
			margin-right: 150px;
		}

	.player-list-container {
		position: relative;
		flex-direction: column;
		display: flex;
		align-items: flex-end;
		display: flex;
		height: 100%;
		overflow-y: auto;
		overflow-x: hidden; 
		-ms-overflow-style: none;  
		scrollbar-width: none; 
		
		.player-list {
			right:0px;
			display: flex;
			flex-direction: column;
			overflow-y: auto; /* Enable vertical scrolling */
			overflow-x: hidden; /* Disable horizontal scrolling */
			-ms-overflow-style: none;  /* Internet Explorer 10+ */
			scrollbar-width: none;  /* Firefox */
			&::-webkit-scrollbar {
				display: none;  /* Safari and Chrome */
			  }
			padding: 10px;
			align-items: center;
			max-width: 500px;
			width: 100%;
			margin-left:auto;
			.player-card {
				position: relative;
				margin-top: 10px;
				border: 1px solid #ccc;
				border-radius: 8px;
				padding: 16px;
				background-color: #fff;
				transition: transform 0.2s, background-color 0.2s;
				box-sizing: border-box;
				display: flex;
				flex-direction: column;
				width: 80%;
				cursor: pointer;
				max-width: 320px;

				&.selected {
					transform: scale(1);
					background-color: #6dc7d3; // Light cyan background for a subtle highlight
					border: 2px solid #00796b; // Teal border for a bold accent
					//box-shadow: 0 1px 20px rgba(253, 249, 5, 0.979); // Subtle shadow for depth
					.character-row-container {
						.character-row-display {
							box-shadow: 0 1px 20px rgba(253, 249, 5, 0.979); // Subtle shadow for depth
						}
					}
				}
				


				.player-card-body{
					height: unset;
					margin-top: auto; /* Push the body to the bottom */
		
				}
				.character-row-container {
					display: flex;
					flex-direction: row;
					align-items: center;
					justify-content: center;
					overflow: hidden;
					.character-row-display {
						display: flex;
						flex-wrap: nowrap;
						gap: 1px;
						
						height: 50%;
						min-height:94px;
						width: fit-content;
						min-width: 90px;
						border: #a3b8b1;
						background: #bee4d7;
						border-style: outset;
						justify-content: center;
						
						.character-collapsed-overlay {
							background-color: red;
							opacity: 0;
							color: black;
							&:hover{
								opacity:.5;
							}
							.character-collapsed-unassign {
								color: black;
								opacity: 1;
								font-size: 60px;
								font-weight: bold;
								z-index: 99999;
								font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
							}
						}
					}
					.character-card.collapsed {
						display: flex;
						align-items: center;
						justify-content: center;
						flex-direction: column;
						
						
					}
					.character-card.collapsed .character-icon {
						display: flex;
						align-items: center;
						justify-content: center;
						flex-direction: column;
						height: 100%;
						
					}
					
					.character-card.collapsed .character-icon img {
						
						width: auto; /* Maintain the aspect ratio */
						object-fit: cover; /* Ensure the image covers the entire container */
						padding: 0px;
					}
					
					.character-card.collapsed .character-initials {
						font-size: 1.2em;
						font-weight: bold;
					}
			
					.character-card.collapsed:hover {
						opacity: .5;
					}
				}
		
			}
			.player-card:hover {
				transform: scale(1.0);
			}
		  }
	}
  }

  .character-assignment {
	flex: 1;
	display: flex;
	flex-direction: column;
	padding: 10px;
	height: 100%; // Ensure the container takes the full height
  }
  
  
  .character-creation-right-container
  {
	display: flex;
	height: 100%;
	overflow-y: auto;
	overflow-x: hidden; 
	-ms-overflow-style: none;  
	scrollbar-width: none; 
	
	width: 60%;
  
  .character-grid {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	overflow-y: auto; // Enable vertical scrolling
	overflow-x: hidden;
	-ms-overflow-style: none;  
	scrollbar-width: none; 
	justify-content: left;
	max-width: 1300px;
	height: 100%; // Ensure the grid takes the full height of the parent
	&::-webkit-scrollbar {
	  display: none; 
	}
	  transition: transform 0.3s ease, opacity 0.3s ease;
	}
  
  .character-grid .character-card {
	flex: 1 1 calc(50% - 20px); 
	box-sizing: border-box;
	margin-top: 10px; 
	margin-left: 10px;


	&.assigned-to-other {
		position: relative;
		&::before {
		  content: 'Assigned to other player';
		  position: absolute;
		  top: 50%;
		  left: 50%;
		  transform: translate(-50%, -50%);
		  background-color: rgba(255, 0, 0, 0.7); // Transparent reddish overlay
		  color: white;
		  padding: 5px 10px;
		  border-radius: 5px;
		  z-index: 1;
		}
		&::after {
		  content: '';
		  position: absolute;
		  top: 0;
		  left: 0;
		  width: 100%;
		  height: 100%;
		  background-color: rgba(255, 0, 0, 0.3); // Transparent reddish overlay
		  z-index: 0;
		}
	  }



  }

  .overlay {
	position: absolute; /* Position the overlay absolutely */
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 1); /* Semi-opaque background */
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	font-size: 1.5em;
	font-weight: bold;
	text-align: center;
	border-radius: 8px; /* Match the border radius of the card */
	animation: pulsate 1.5s infinite, shine 2s infinite; /* Pulsating opacity and shiny effect */
	z-index: 1; /* Ensure the overlay is above other elements */
	overflow: hidden; /* Hide overflow for the shiny effect */
	.overlay-text {
		top: 50px;
		position: relative;
	}
  }
  

  @keyframes pulsate {
	0% {
	  opacity: 0.5;
	}
	50% {
	  opacity: 0.7;
	}
	100% {
	  opacity: 0.5;
	}
  }
  
  @keyframes shine {
	0% {
	  background-position: -200%;
	}
	100% {
	  background-position: 200%;
	}
  }
  
  .overlay::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 200%;
	height: 100%;
	background: linear-gradient(
	  120deg,
	  rgba(255, 255, 255, 0.2) 0%,
	  rgba(255, 255, 255, 0.5) 50%,
	  rgba(255, 255, 255, 0.2) 100%
	);
	animation: shine 2s infinite;
  }
}
}