.group-schedule-holder {
    width: 100%;
    margin-top: 390px;
    .group-schedule-container {
        position: relative;
        background-color: #f0f0f0;
        border: 1px solid #ccc;
        border-radius: 8px;
        padding: 10px;
        width: 100%;
        max-width: 65%;
        margin: 0 auto;
        left: -10px;
    }

    .group-schedule-tab {
        position: absolute;
        top: -20px;
        left: 10px;
        background-color: #fff;
        padding: 5px 10px;
        border: 1px solid #ccc;
        border-radius: 8px 8px 0 0;
        font-weight: bold;
    }

    .week-display-days {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
    }

    .day-box {
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 4px;
        width: 13%;
        padding: 10px;
        text-align: center;
        position: relative;
    }

    .day-abbreviation {
        position: absolute;
        top: 5px;
        left: 5px;
        font-weight: bold;
    }

    .day-percentage {
        margin-top: 20px;
        font-size: 1.2em;
        font-weight: bold;
    }
}