:root {
    --notebook-bg-color: #ececec;
    --notebook-text-color: #716f6d;
    --notebook-header-font-size: 2.8rem;
    --notebook-header-font-size-small: 1.9rem;
    --notebook-header-font-size-xsmall: 1.5rem;

    --notebook-bg-color-player: #bed0ed;
    --notebook-bg-color-player-highlight: #a2cce9;
    --notebook-bg-color-player-selected: #8ac3e9;


    
    --notebook-bg-color-character: #d7e6d5;
    --notebook-bg-color-character-highlight: #c4e9bf;
    --notebook-bg-color-character-selected: #abe6a3;


    --notebook-bg-color-encounter: #e2d6c7;
    --notebook-bg-color-location: #d6cddf;
    --notebook-text-color-player: #81997d;
    --notebook-text-color-encounter: #a8a63b;
    --notebook-text-color-location: #8175b0;
    --notebook-text-color-character: #e24646;

    --notebook-bg-color-group: #ecbd92;

    --notebook-color-conflicted-main: #7e7e7e;
    --notebook-color-like-main: #3d8b35;
    --notebook-color-hate-main: #c33e3e;
}


.notebook-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--notebook-text-color);
    font-style: italic;
    z-index: 9999;
}

.notebook-content {
    background: white;
    width: 65%;
    height: 80%;
    padding: 0px;
    overflow-y: scroll;
    border: 1px solid #8b8b8b;
    background-image: linear-gradient(to bottom, var(--notebook-bg-color) 30px, #a89e9e 31px);
    background-size: 100% 31px;
    position: absolute;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
    &::-webkit-scrollbar {
        display: none;  /* Safari and Chrome */
      }
    &.o1 {
       
        margin-left: -30px;
        margin-top: -18px;
    }
    &.o2 {
        margin-top: -9px;
        margin-left: -15px;
        overflow: visible;
    }
    .notebook-header {
        position: relative;
        &.player{
            background: var(--notebook-bg-color-player);
        }
        
        &.encounter{
            background: var(--notebook-bg-color-encounter);
        }

        &.location {
            background: var(--notebook-bg-color-location);
        }
        &.character {
            background: var(--notebook-bg-color-character);
        }
        &.group {
            background: var(--notebook-bg-color-group);
        }
        .notebook-header-title{
            color: var(--notebook-bg-color);
            position: absolute; // Use absolute positioning
            top: 35%; // Center vertically
            left: 55px;
            transform: translate(0%, -50%); // Adjust position to be centered
            font-size: 5rem;
            pointer-events: none; 
            font-style: normal;
            font-weight: bold;


        }
    }
    


}

.notebook-tabs {
    position: relative;
    top: -52px;
    left: 7px;
    display: flex;
    cursor: pointer;
    &.o2 {
        overflow: visible;
    }
    .notebook-tab {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 20px;
        background: var(--notebook-bg-color);
        border: 1px solid #8b8b8b;
        margin-right: 2px;       
        z-index: 9999;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        margin-top: 6px;
        margin-bottom: -15px;
        border-bottom: none;
        transform: translate(0px, -4px);
        &.current {
            margin-top: 10px;
            left: 0;
            transform: translate(0px, 0px);
        }
        &.player {
            background: var(--notebook-bg-color-player);
        }
        &.encounter {
            background: var(--notebook-bg-color-encounter);
        }
        &.location {
            background: var(--notebook-bg-color-location);
        }
        &.character {
            background: var(--notebook-bg-color-character);
        }
        &.group {
            background: var(--notebook-bg-color-group);
        }
        .notebook-tab-title {
            font-size: 20px;
            font-weight: bold;
        }
    }
}

.notebook-header {
    height:123px;
    width: 100%;
    top: 0px;
    background-color: var(--notebook-bg-color);
    display: flex;
    align-items: flex-end;
    .title-bar {
        display: flex;
        width: 100%;
        .title-text, .features-text {
            flex: 1;
            text-align: left;
            font-size: xx-large;
            padding: 0 16px;
            position: relative;
            bottom: -10px;
          }
          .features-text {
            text-align: left; /* Ensure text is left-justified */
          }

    }
}


// -------------------------------------------------------------------------------------------------------------------
// ENCOUNTER
// -------------------------------------------------------------------------------------------------------------------
.notebook-encounter-container {
    display: flex;
    align-items: flex-end;
    width: 100%;
    position: relative;
    align-items: flex-start;
    .notebook-encounter-contents {
        flex: 1;
        text-align: left;
        font-size: xx-large;
        padding: 0 16px;
        position: relative;
        bottom: -10px;

        .notebook-icon-container {
            margin-top: 10px;
            width: 100%;
            height: auto;
            position: absolute;
            .cimage {
                width: 90%;
            }
        }
    }
}



// -------------------------------------------------------------------------------------------------------------------
// LOCATION
// -------------------------------------------------------------------------------------------------------------------
.notebook-location-container {
    display: flex;
    align-items: flex-end;
    width: 100%;
    position: relative;
    align-items: flex-start;
    .notebook-location-contents {
        flex: 1;
        text-align: left;
        font-size: xx-large;
        padding: 0 16px;
        position: relative;
        bottom: -10px;

        .notebook-icon-container {
            margin-top: 30px;
            margin-right: 30px;
            width: 100%;
            height: auto;
            position: absolute;
            .cimage {
                width: 90%;
            }
        }
    }
}

.elements-features-list-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 0px; // Adjust the gap between items as needed
    .elements-features-list {
        white-space: nowrap;
    }
  }

.pill-button {
    position: absolute;
    top: 10px;
    right: 10px;
    border-radius: 100%;
    color: var(--info-display-pencil);
    border: 3px solid var(--info-display-pencil);
    background: var(--notebook-bg-color);
   
    font-size: 20px;
    font-style: bold;
    font-weight: 900;
    transition: .2s;
    cursor: pointer;
    &:hover {
        color: var(--info-display-pen);
    border: 3px solid var(--info-display-pen);
    transform: scale(1.1);
    }
    &.remove {
       border-radius: 15px;
       white-space: nowrap;
    }
}

.notebook-text {
    left: 10px;
    top: 1px;
    position: relative;
    font-size: 20px;
    margin-right:40px;
    line-height: 30px;
    &.player{
        top: 12px;
        
    }
    &.large {
        font-size: 34px;

    }
}

.notebook-header-container {
    display: flex;
    align-items: flex-start;
    width: 100%;
    padding: 0px 10px;
    bottom: 4px;
  
   

    .title-text {
        flex: 1;
        top: 12px;
        position: relative;
        text-align: left;
        font-size: var(--notebook-header-font-size);
        font-style: normal;
        font-weight: bold;
        &.small {
            top: 29px;
            font-size: var(--notebook-header-font-size-small);
        }
    }

    .player-preferences-container {
        display: flex;
        flex: 1;
        justify-content: space-between;
        text-align: left;

        .player-preferences {
            flex: 1;
            display: flex;
            flex-direction: column;
            text-align: left;
            top: 27px;
        }
    }
}


// -------------------------------------------------------------------------------------------------------------------
// PLAYER
// -------------------------------------------------------------------------------------------------------------------

.notebook-player-container {
    display: flex;
    align-items: flex-start;
    width: 100%;
    padding: 0px 10px;

    .title-text {
        flex: 1;
        top: 10px;
        position: relative;
        text-align: left;
        font-size: xx-large;
    }
    .notebook-player-content {
        display: flex;
        position: relative;
        top: 8px;
        .notebook-text {
            left: 35px;
        }

        .notebook-player-icon {
            width: 100px;
            height: 100px;
            position: relative;
            top: 10px;
            left: 10px;
            .cimage {
                width: 100%;
                border-radius: 100%;
            }
        }
    }

    .player-preferences-container {
        display: flex;
        flex: 0 0 50%; // Take up 50% of the parent
        justify-content: space-between;
        text-align: left;
        flex-direction: row;
        top: 8px;
        
        padding: 0px 10px;

        .player-preferences {
            flex: 1;
            display: flex;
            flex-direction: column;
            text-align: left;
            position: relative;
            left: -18px;
            top: -5px;
            padding-right: 30px;
            &.hates {
               left: 2px;
            }
          
        }
        .player-preference-title-small {
            display: none;
            &.single-column {
                display: contents;
            }
        }
   
    }

    .preference-pill {
        border-radius: 15px;
        height: fit-content;
        width: fit-content;
        position: relative;
        height: 24px;
        top: 4px;
        white-space: nowrap;
        padding: 0px 8px; // Adjust padding to make the pill smaller
        margin: 2px 15px; // Use negative margins to offset the padding
        &.hate {
            background-color: #e8868667;
            border: 2px solid #92555567;
        }
        &.like {
            background-color: #86e8a667;
            border: 2px solid #55925667;
        }
        &.trait {
        
            display: inline-block; 
            background-color: #d386e867;
            border: 2px solid #79559267;
        }
        .notebook-text {
            top: -2px;
            left: 1px;
        }
      }

    .player-notebook-right-content{
        display: flex;
        flex-direction: column;
        flex: 0 0 50%; // Take up 50% of the parent
        max-width: 50%;
        .player-schedule-legend {
            scale: 50%;
            display: flex;
            width: 100%;
            justify-content: center;
            position: relative;
            margin-bottom: 8px;
            .week-display-days {
                width: unset;
            }
        }
    }

    .player-notebook-left-content{
        display: flex;
        flex-direction: column;
        flex: 0 0 50%; // Take up 50% of the parent
        max-width: 50%;
        .player-traits {
            position: relative;
            top: 32px;
            .notebook-text {
                display: flex;
                flex-wrap: wrap; // Ensure the elements wrap to the next line when overflowing
                gap: 5px; // Add some space between the elements
              }
              
        }
        .player-characters{
            position: relative;
            top: 22px;
            margin-top: 36px;;
        }
    }
    .trait-description {
        display: inline-block; // Display as inline-block to behave like words
        line-height: 28px;
        left: 20px;
        position: relative;
      }
}


// -------------------------------------------------------------------------------------------------------------------
// GROUP
// -------------------------------------------------------------------------------------------------------------------
.player-recruitment-mode {
    .notebook-modal {
        .notebook-content {
            height: 50%;
            top: 100px;
        }
    }
}

.notebook-group-container {
    font-size: large;
    align-items: center; // Center vertically

    .notebook-group-players {
        top: 13px;
        .notebook-group-players-list {
            display: flex;
            flex-wrap: wrap; // Allow items to wrap to the next line if needed
            margin-top: 10px;
            justify-content: center;;
          }
          
          .player-name {
            display: inline-block; // Ensure each player name behaves like an inline element
           
            border-radius: 15px; // Optional: Add border radius for pill shape
            line-height: 2em;
          }
    }

    .notebook-group-top-content {
        display: flex;
        
        justify-content: center;
        position: relative;
    }

    .player-attitude {
        .attitude-column {
            margin-top: 39px;
            display: flex;
           
            align-items: center; // Center vertically
            flex-direction: column; //
           
        }
        
    }
    .player-attitude-conflicted {
        align-items: center; // Center vertically
        display: flex;
        .attitude-item {
            top: 4px;
        }
    }
    .attitude-item {
            position: relative;
            margin-bottom: -9px;;
            height: 10px;
            top: -6px;
            width: fit-content;
            font-weight: bold;
           .attitude-item-text {
            top: -8px;
            position: relative;
            text-align: center;
           }
        }
}




// -------------------------------------------------------------------------------------------------------------------
// Character
// -------------------------------------------------------------------------------------------------------------------

.notebook-character {
    width: 100%;
    display: flex;
    flex-direction: row;
    .notebook-character-left,
    .notebook-character-right {
    flex: 1; /* Each will take up 50% of the space */
    padding: 10px; /* Optional: Add padding if needed */
    box-sizing: border-box; /* Ensure padding and border are included in the element's total width and height */
  
    }
    .notebook-text {
        line-height: 31px;
    }

    .header-text {
        font-size: var(--notebook-header-font-size-small);
    }

    .attribute-display {
        font-size: 20px;
    }

    .notebook-character-left {
        .notebook-left-top-container{
            display: flex;
            flex-direction: row;
            .notebook-character-icon-container { 
                width: 115px;
                height: 115px;
                position: relative;
                top: 0px;
                left: 10px;
                .cimage {
                    width: 100%;
                    border-radius: 100%;
                }
            }
            
            .notebook-character-stats-container {
                top: -2px;
                margin-left: 10px;
                position: relative;
            }
        }

        .notebook-character-combined-abilities {
            top: -2px;
            .notebook-character-abilities-title{
                top: 10px;
                margin-top:26px;
               
            }
            .notebook-character-abilities {
                margin-top: 4px;
            }
        }
    }

    .notebook-character-right {
        width: 50%;
        line-height: 31px;
       
        .notebook-right-container{
            display: flex;
            flex-direction: column;
            position: relative;
            top: -4px;
            .notebook-character-ability-breakdown-container {
                margin-left: 10px;
                .notebook-character-race-abilities {
                    display: flex;
                    flex-direction: row;
                }
                .notebook-character-ability-breakdown {
                    top: -2px;
                    position: relative;
                }
                .notebook-character-ability-levels {
                        
                    margin-left: 40px;
                }
                .gear-attributes{
                    display: flex;
                    flex-direction: row;
                    
                    margin-left: 40px;
                }
                .level-attributes {
                    display: flex;
                    flex-direction: row;
                    gap: 0px;
                }

                .attribute-display-container {
                    
                    .collapsed-attributes{ 
                        margin-left: 12px;
                    }
                    
                }
               
            }
           
        }
        
    }
    .attribute-display-container{
        .attribute-icon {
            border-radius: 100%;
            height: 30px;
            aspect-ratio: 1 / 1; /* Maintain a 1:1 aspect ratio */
            background-color: #8a8a8a;
            display: inline-block; /* Ensure the element respects the aspect ratio */
            background-position: center; /* Center the background */
            text-align: center; 
            margin-right: 2px;
            margin-bottom: 1px;
            &.combat {
                background-color: #c27039;
            }
            &.magic {
                background-color: #9d49a7;
            }
        }
    }
}





// -------------------------------------------------------------------------------------------------------------------
// Single-column
// -------------------------------------------------------------------------------------------------------------------

.notebook-modal.single-column {
 
    .title-text{
        display: none;
        &.lower {
            display: inline-block;
            top: 0px;
            position: relative;
            font-size: 2.1em;
            font-weight: bold;
        }
    }
    .notebook-player-container {
        position: relative;
        top: 6px;
        flex-direction: column;
    }
    .notebook-header {
        height: 5.2em;
        .notebook-header-title {
            top: 50%;
        }
    }
    
    .notebook-content {
        background-image: linear-gradient(to bottom, var(--notebook-bg-color) 20px, #a89e9e 21px);
        background-size: 100% 21px;
        display:flex;
        flex-direction: column;
    }

    .notebook-text {
        line-height: 21px;
    }
    .notebook-tabs {
        top: -3.1em;
        
    }
    .notebook-tab{
        &.current{
            top: 4px;
            position: relative;
        }
    }
    
    .player-preferences-container{
        left: 5px;
        top:20px;
        .player-preference-title-small {
            display: inline-block;
            left: 10px;
            position: relative;
            margin-left: -10px;
            padding-left: 20px;
            font-size: 1.1em;
            font-weight: bold;
        }
        .title-text{
            top: 0px;
            display: none;
        }
        .player-preferences {
            .preference-pill {
                margin-top: -.1em;;
                height: .9em;
                margin-bottom: .2em;
                .notebook-text {
                    top: -.2em;
                    
                }
            }
            .trait-description {
                line-height: 19px;
            }
        }
    }
.player-notebook-right-content {
    max-width: 100%;
}

.player-schedule-container {
    left: 10%;;
    width:80%;
    bottom: 20%;
    margin-top: 22px;
   
}
   
}