
.campaign-display-body {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.encounter-row {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.encounter-pill {
    padding: 10px 20px;
    border-radius: 20px;
    background-color: #e0e0e0;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    width: -webkit-fill-available;
}