/* Import the themes.css file */
@import url('../styles/themes.css');

.encounter-display {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 10px;
    border: 6px solid #000000;
    margin: 5px;
    font-size: large;
    color:#bebebe;
    font-weight: bold;
    width: -webkit-fill-available;
    max-width: 50%;
    height: 60px;
    cursor: pointer;
    transition: 1s;
    position:relative;
    /* &:hover {
        transform: scale(1.05);
    } */
    .encounter-name {
        font-size: 1.3em;
        font-weight: bold;
        position: relative;
    }
}

.encounter-display.forest {
    color: var(--forest-display-pen);
    border-color: var(--forest-display-pencil);
    &:hover {
        border-color: var(--forest-display-pen);
        background-color: var(--forest-display-pen-semi);
    }
}

