
:root {
    --chat-display-toggle-height: 50px;
    --chat-background-color: #ab72bc;
    --chat-background-color-trans: #ab72bcb4;
}

.chat-container {
    position: fixed;
    bottom: 0;
    left: 10px;
    width: 25%;
    z-index: 99999;
    
}

.chat-toggle {
    position: relative;
    height:var(--chat-display-toggle-height);
    border-radius: 5px 5px 0 0;
    background-color: #ab72bc00;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 80px;
    .chat-toggle-icon {
       position: relative;
       width: 80px;
       height: 80px;
       margin-bottom: 10px;
       cursor: pointer;
       .chat-toggle-icon-text {
            position: absolute;
            top: 27px;
            left: 25px;
            color: white;
            font-weight: bold;
       }
    }
    .chat-toggle-text {
        position: static;
        text-align: center;
        &.pill-button {
            padding-left: 40px;
            padding-right: 40px;
            border-radius: 15px;
            color: var(--info-display-pen);
            border-color: var(--info-display-pen);
           
        }
    }
    &.expanded {
        background-color: #ab72bc;
        width: unset;
        height: 50px;
        .chat-toggle-icon {
           display: none;
        }
    }
    

}

.chat-expanded {
    position: absolute;
    bottom: var(--chat-display-toggle-height); // Position it above the toggle
    left: 0;
    width: 100%;
    background-color: #ab72bcb4;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 5px 5px 0 0;
    z-index: 1; // Ensure the expanded chat is behind the toggle
    .chat-expanded-container {
        
        .chat-expanded-messages {
            display: flex;
            flex-direction: column;
            margin-top: 10px;
            .chat-message-container{
                margin-bottom: 10px;
                flex-direction: row;
               
               
                &:nth-child(odd) { 
                    .chat-message-display {
                    flex-direction: row-reverse; // Swap the order for odd messages
                    margin-left: 70px;
                    margin-right: 0px;
                  }
                }

                &:nth-child(even) { 
                    .chat-message-display {
                        margin-right: 70px;
                    }
                }
                .chat-message-display{
                    display: flex;
                    flex-direction: row;
                    .chat-message-icon{
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        background-color: #81aee3;
                        margin: 10px;
                    }
                
                    .chat-message-text{
                        border-radius: 5px;
                        background: white;
                        padding: 10px;
                        min-height: 50px;
                        width: calc(100% - 50px);
                        color: var(--notebook-text-color)
                    }
                }
            }
        }
    }
}


.chat-new-message {
    position: absolute;
    bottom: 100px; // Adjust as needed
    left: 10px;
    width: calc(100% - 20px);
    background-color: var(--chat-background-color-trans);
    color: white;
    padding: 10px;
    border-radius: 5px;
    animation: fadeOut 5s forwards;
  
    @keyframes fadeOut {
      0% {
        opacity: 1;
      }
      50% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }

    .chat-message-display {
        display: flex;
        flex-direction: row;
        .chat-message-icon{
            width: 150px;
            height: 150px;
            border-radius: 50%;
            background-color: #81aee3;
            margin: 10px;
        }
    
        .chat-message-text{
            border-radius: 5px;
            background: white;
            padding: 10px;
            min-height: 100px;
            width: calc(100% - 50px);
            color: var(--notebook-text-color);
            font-size: 2em;
        }
    }
  }
