.campaign-display-container{
	display: flex;
	width: 100%;
	margin-left: 20px;
}
.campaign-setup-container {
	display: flex;
	height: 100vh; 


	.player-list-container {
		height: 100vh;
		position: relative;
		flex-direction: column;
		display: flex;
		width: 30%;
		margin-top: 16px;
		max-width: 340px;
	}

	.menu-button.campaign-setup  {
		height: 50px;
		position: sticky;
		.label-text {
			top: 6px;
		}
	}

	.player-list {
		display: none;
		flex: 1;
		display: flex;
		flex-direction: column;
		overflow-y: auto; /* Enable vertical scrolling */
		overflow-x: hidden; /* Disable horizontal scrolling */
		-ms-overflow-style: none;  /* Internet Explorer 10+ */
		scrollbar-width: none;  /* Firefox */
		&::-webkit-scrollbar {
			display: none;  /* Safari and Chrome */
		  }
		padding: 10px;
		.player-card {
			position: relative;
			margin-top: 10px;
			border: 1px solid #ccc;
			border-radius: 8px;
			padding: 16px;
			background-color: #fff;
			transition: transform 0.2s, background-color 0.2s;
			box-sizing: border-box;
			display: flex;
			flex-direction: column;
			cursor: normal;
			.player-card-body{
				height: unset;
				margin-top: auto; /* Push the body to the bottom */
	
			}
			.character-row-display {
				display: flex;
				flex-wrap: nowrap;
				gap: 1px;
				margin-top:auto;
				max-height: 74px;
				min-height: 74px;
				border: #a3b8b1;
				background: #bee4d7;
				border-style: outset;
			  }
			.character-card.collapsed {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				
				max-height: 100%; 
				max-width: 25%;
			}
			.character-card.collapsed .character-icon {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				height: 100%;
				min-width: 350%;
			  }
			  
			  .character-card.collapsed .character-icon img {
				
				width: auto; /* Maintain the aspect ratio */
				object-fit: cover; /* Ensure the image covers the entire container */
				padding: 0px;
			  }
			
			  .character-card.collapsed .character-initials {
				font-size: 1.2em;
				font-weight: bold;
			  }
	
			  .character-card.collapsed:hover {
				transform: scale(1.05);
				color: rgb(134, 134, 134);
			  }
	
		}
		.player-card:hover {
			transform: none;
		}
	  }

	.campaign-list-container {
		flex: 3;
		display: flex;
		flex-direction: column;
		padding: 10px;
		overflow-y: auto;
		-ms-overflow-style: none;  /* Internet Explorer 10+ */
		scrollbar-width: none;  /* Firefox */
		&::-webkit-scrollbar {
			display: none;  /* Safari and Chrome */
		}
	}
}