.notebook-modal.tutorial {
    top: 0px;
    background-color: #000000cc;
    width: 100%;
   

    .notebook-content {
        &.o1 {
            display: none;
        }
        &.o2 {
            display: none;
        }
        &.o3 {
            display: none;
        }
      }

            .notebook-tutorial-container {
                display: flex;
                flex-direction: row;
                width: 100%; // Ensure it takes the full width
                height: 100%; // Ensure it takes the full height
                max-width: 1800px;
        
                .notebook-tutorial-contents {
                    display: flex;
                    flex: 1; // Allow contents to grow and fill the container
                     position: relative;
                    max-width: 50%;
                     &.left {
                       display: flex;
                       flex-direction: column;
                       align-items: center;
                       justify-content: center;
                       gap: 40px;
                       .pill-button {
                            display: flex;
                            font-size: large;
                            padding: 4px;
                            margin-top: 40px;
                            color: var(--notebook-text-color);
                            border-color: var(--notebook-text-color);
                            position: static;
                       }
                       .notebook-icon-container {
                           width: 100%;
                           align-items: center;
                           display: flex;
                           justify-content: center;
                           .cimage{
                                 width: 80%;
                                 height: auto;
                                 max-width: 500px;;
                           }
                        }

                        .tutorial-title-container{
                            width: 80%;
                            max-width: 500px;;
                            .tutorial-title {
                                font-size: 3em;
                                text-align: center;
                            }
                        }


                    }

                    &.right {
                        display: flex;
                        flex-direction: column;
                        background: white;
                        border-radius: 20px;
                        padding: 40px;
                        margin: 40px;
                        .tutorial-message-container {
                            .tutorial-message {
                                font-size: larger;
                            }
                        }
                        .notebook-tutorial-buttons {
                          
                            bottom: 0px;
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            .nav-button {
                                color: var(--notebook-text-color);
                                border-color: var(--notebook-text-color);
                                margin-bottom: 40px;
                                position: absolute;
                                bottom: 0px;
                                font-style: normal;
                                font-weight: bold;
                                font-size: 1.4em;
                                &.next {
                                    right: 40px;
                                }
                                &.prev {
                                   left: 40px;
                                }
                            }
                            }

                        
                    
                    }

                }
            }


}