.campaign-display {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin-top: 10px;
  border-style: solid;
  border-color: grey;
  border-width: 5px;
  max-width: 100%;
  background-color: rgb(255, 0, 162);
  width: -webkit-fill-available;
  transition: transform 0.2s, background-color 0.2s, border-color 0.2s;
  border-radius: 50px;
  padding: 10px 20px;
  justify-content: space-between;
  cursor: normal;
  background-size: 25px 25px;
  background-image: 
    linear-gradient(to right, #88888840 1px, transparent 1px),
    linear-gradient(to bottom, #88888840 1px, transparent 1px);

}
.campaign-display.collapsed {
  background-image: none;
  &:hover {
    transform: scale(1.05);
  }
  cursor: pointer;
}

.campaign-display.full {
  flex-direction: column;
}

.campaign-display-header {
  display: flex;
  flex-direction: row;
  align-items: center; // Align items vertically in the center
  width: -webkit-fill-available;

  .campaign-name {
    font-weight: bold;
    margin-bottom: 5px;
  }

  .campaign-info {
    flex: 1 1 auto;
    text-align: center;

    .campaign-description {
      font-size: 1.1em;
      margin-left: 20px;
      margin-right: 20px;
    }
  }
  .campaign-extra {
    flex: 0 0 auto;
    margin-left: 20px;
    text-align: right;
  }
  .campaign-icon {
    flex: 0 0 auto;
    margin-right: 20px;
    max-width: 100px;
    max-height: 100%;
    display: relative;

    img {
      max-width: 100%;
      max-height: 100%;
      border-radius: 100%;
      border: 4px solid darkgreen;
    }
  }
}

.campaign-display-body {
  flex: 1; // Allow the body to grow and take up available space
  min-height: 200px;
  width: -webkit-fill-available;
}

.campaign-display-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 10px;
  width: -webkit-fill-available;

  .ui-actions {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  .campaign-button {
    height: 100px; /* Ensure the height matches the footer height */
    width: 100px; /* Ensure the width matches the height for round buttons */
    border-radius: 100%;  
    border-style: solid;
    
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    cursor: pointer;
    transition: .5s;
    &:hover {
      transform: scale(1.05);
      border-width: 10px;
      border-color: white;
    }
  }
  .campaign-button.accept {
    background-color: #ffffff00;
    margin-left: auto;
  }
  
  .campaign-button.cancel {
    background-color: #ffffff00;
    margin-right: auto;
  }
}

// Theme overrides: 
.campaign-display {


  // DARK - ----------------------------------------------------------------------------
  &.dark {
    border-color: var(--dark-primary-color);
    background-color: var(--dark-paper-color);
    color: var(--dark-primary-color);
    .campaign-display-footer {
      .ui-actions{
        .campaign-button{
          color: var(--dark-primary-color);
          border-color: var(--dark-primary-color);
          &.accept {
            &:hover {
              transform:scale(1.2);
              border-color: var(--dark-glow-color);
              color: var(--dark-glow-color);
            }
          }
        }
       
      }
    }
    .campaign-icon img {
      border-color: var(--dark-glow-color);
    }

    &.collapsed {
    
      border-color: var(--dark-primary-color);
      background-color: var(--dark-background-color);
      .campaign-icon img {
        border-color: var(--dark-primary-color);
      }
      
      .campaign-icon img::before {
        background: var(--dark-glow-color);
        transform: scale(.9);
        z-index: -1;
      }
      &:hover {
        border-color: var(--dark-secondary-color);
        background-color: var(--dark-primary-color);
        color: var(--dark-secondary-color);
        .campaign-icon img {
          border-color: var(--dark-glow-color);
          
          &::before {
            transform: scale(1.5);
            box-shadow: 0 0 15px var(--dark-glow-color);
          }
          color: var(--dark-glow-color);
          box-shadow: 0 0 5px var(--dark-glow-color);
          text-shadow: 0 0 5px var(--dark-glow-color);
        }
      }
    }
  }

   // DARK - ----------------------------------------------------------------------------
   &.adventure {
    border-color: var(--adventure-primary-color);
    background-color: var(--adventure-paper-color);
    color: var(--adventure-primary-color);
    .campaign-display-footer {
      .ui-actions{
        .campaign-button{
          color: var(--adventure-primary-color);
          border-color: var(--adventure-primary-color);
          &.accept {
            &:hover {
              transform:scale(1.2);
              border-color: var(--adventure-glow-color);
              color: var(--adventure-glow-color);
            }
          }
        }
       
      }
    }
    .campaign-icon img {
      border-color: var(--adventure-glow-color);
    }

    &.collapsed {
    
      border-color: var(--adventure-primary-color);
      background-color: var(--adventure-background-color);
      .campaign-icon img {
        border-color: var(--adventure-primary-color);
      }
      
      .campaign-icon img::before {
        background: var(--adventure-glow-color);
        transform: scale(.9);
        z-index: -1;
      }
      &:hover {
        border-color: var(--adventure-secondary-color);
        background-color: var(--adventure-primary-color);
        color: var(--adventure-secondary-color);
        .campaign-icon img {
          border-color: var(--adventure-glow-color);
          
          &::before {
