
.character-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.character-info {
  flex: 2; /* Occupy 2/3 of the row */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.character-name {
  text-align: center;
  font-size: 1.6em;
  font-weight: bold;
  color: #555;
}

.character-details {
  display: flex;
  justify-content: space-around;
  font-size: larger;
  font-weight: bold;
  margin-top: -4px;
  color: #555;
}


.character-race,
.character-class {
  margin-bottom: 5px;
}

  .character-icon img {
	max-width: 100%; /* Ensure the image scales to fit the container width */
	max-height: 100%; /* Ensure the image scales to fit the container height */
	width: auto; /* Maintain the aspect ratio */
	height: auto; /* Maintain the aspect ratio */
	object-fit: cover; /* Ensure the image covers the entire container */
	display: block; /* Ensure the image is treated as a block element */
  }
  
  .character-level {
	text-align: center;
	margin-top: 5px; 
	font-size: larger;
  }
.character-card {
	border: 1px solid #ccc;
	border-radius: 8px;
	padding: 16px;
	background-color: #fff;
	transition: transform 0.2s, background-color 0.2s;
	box-sizing: border-box;
	color: #aaa;
	cursor: pointer;
	max-width: 40%;
	position: relative;
	transition: transform 0.3s ease, opacity 0.3s ease; /* Add transition for smooth effect */
	.attribute-display-container {
		display: flex;
		flex-direction: column;
		align-items: left;
		.attribute-display {
			margin-left: 20px;
			display: flex;;
		}
	}
  }
  
  .character-card-container.assigned {
	opacity: 0.2; /* Slightly lower opacity for assigned characters */
  }


  .character-card:hover {
	transform: scale(1.05);
  }
  
.character-card.hidden {
	display: flex;
}
.character-assigned {
	font-size: large;
	font-weight: bold;
	bottom: 40px;
    position: relative;
    height: 0px;
}

  .character-card.selected {
	background-color: #e0f7fa; /* Slightly different background color when selected */
	border-color: #00796b; /* Different border color when selected */
	&::before {
		content: 'Assigned';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #00796b; // Transparent reddish overlay
		color: white;
		padding: 5px 10px;
		border-radius: 5px;
		z-index: 1;
	  }
	  &::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #00796b55; // Transparent reddish overlay
		z-index: 0;
	  }
  }
  
  .character-card-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	.character-info{
		flex: 0 0 75%; // Take up 75% of the parent's width
		flex-shrink: 0; // Prevent shrinking
		overflow: hidden; // Hide overflow content
		text-overflow: ellipsis; // Add ellipsis for overflow text
		white-space: nowrap; // Prevent text wrapping
	}
	.character-icon {
		flex: 0 0 25%; // Take up 25% of the parent's width
		flex-shrink: 0; // Prevent shrinking
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
  }
  
  .character-info {
	flex: 2; /* Occupy 2/3 of the row */
	display: flex;
	flex-direction: column;
	justify-content: center;
  }
  
  .character-level,
  .character-name,
  .character-class {
	margin-bottom: 5px;
  }
  
  .character-icon {
	
	display: flex;
	align-items: center;
	justify-content: center;
  }
  
  .character-icon img {
	max-width: 100%; /* Ensure the image scales to fit the container width */
	max-height: 100%; /* Ensure the image scales to fit the container height */
	width: auto; /* Maintain the aspect ratio */
	height: auto; /* Maintain the aspect ratio */
	object-fit: cover; /* Ensure the image covers the entire container */
	display: block; /* Ensure the image is treated as a block element */
  }
  
  .character-card-body {
	margin-top: 16px;
  }
  
  .abilities-list ul {
	list-style-type: none;
	padding: 0;
  }
  
  .abilities-list li {
	margin-bottom: 8px;
  }

 .character-collapsed-overlay {
	width: 100%;
	height: 100%;
	border-radius: 8px;
	position: absolute;
	top: 0px;
	background-color: rgba(176, 192, 250, 0);
	.more-info{
		margin-right:10px;
		opacity: 0;
		border: 1px solid #ccc;
	}
	&:hover {
		background-color: rgba(176, 192, 250, .5);
		.more-info{
			opacity: 1;
		}
	}

	
	
 }